import { observer } from 'mobx-react';
import { useEffect, useState } from 'react';
import StatisticStore from '../../../stores/StatisticStore';
import './MarketIndex.scss';
import { Movements } from '../../../services/WatchlistService';
import { useHistory } from 'react-router';
import { StockLogo } from '../../../components/StockLogo';
import { Loading_icon } from '../../../images/Loading';
import { indexStore, SearchType } from '../../../stores/IndexStore';
import { IndexTabs } from '../../IndexTabs';
import { TabsConfig } from '../../../config/TabsConfig';
import Routes from '../../../components/Routes';



export const MarketIndexPage = observer((props: any) => {
    useEffect(()=>{
    indexStore.searchType = SearchType.stock;
    return () => {
            indexStore.searchType = undefined;
        }
    },[]);
    return (
        <>
            <IndexTabs tabs={TabsConfig.marketTabs} />
            <div className="appContent">
                <Routes routes={props.routes} />
            </div>
        </>
    )
});