import './Main.scss';
import { observer } from 'mobx-react';

import { Layout, Menu } from 'antd';
import { useState } from 'react';
import imgFold from '../../images/main/fold.png';
import imgExpand from '../../images/main/expand.png';
import { TabsConfig } from '../../config/TabsConfig';
import { translate } from '../../components/Text';
import { userStore } from '../../stores/UserStore';
import GlobalConfig from '../../config/GlobalConfig';
import { Profile } from '../Profile/Profile';
import { LocalStorageService } from '../../utils/storage';
import { LoginModel, loginStore } from '../../stores/LoginStore';
import { mainStore } from '../../stores/MainStore';
import { IconMore } from '../UserCenter/Icons';
import eventBus from '../../utils/eventBus';
import { Icon_logo_gold } from '../../images/index/logoGold';
import { getRem } from '../../utils/globalFuncs';
import { px } from '../../utils/rem';
import { Icon_avatar } from '../../images/index/avatar';

const { Sider } = Layout;

export const SideTabs = observer(() => {
  const [collapsed, setCollapsed] = useState(false);
  const [visible, setVisible] = useState(false);
  let links = TabsConfig.sideTabs.map((t) => t.link);
  var width = px(236);
  mainStore.sideWidth = width;
  if (!collapsed) {
    mainStore.tabWidth = document.body.clientWidth - width;
  }
  eventBus.addListener('resize', () => {
    mainStore.sideWidth = px(236);
    mainStore.tabWidth = document.body.clientWidth - px(236);
  });
  let hash = window.location.hash;
  let hashIdx = links.indexOf(hash.slice(1));
  if (hashIdx > -1) {
    mainStore.menuKey = hashIdx;
  } else if (hash.indexOf("stock") > -1) {
    mainStore.menuKey = 2;

  } else if (hash.indexOf("myCenter") > -1) {
    mainStore.menuKey = 5;
  }
  return <Sider className="sideBarWrapper" width={mainStore.sideWidth} collapsible collapsed={collapsed} >

    <div className="topWrapper">
      <div className={`topContent ${collapsed ? 'collapsed' : ''}`} >
        <img className='logo' src={Icon_logo_gold} alt="" onClick={() => {
          GlobalConfig.history.push('/');
        }} />
        {!collapsed && <span className='name'>OLI VIEW</span>}

      </div>

      <div className={`userContent ${collapsed ? 'collapsed' : ''}`} onClick={() => {
        let isLogin = LocalStorageService.get("token");
        if (!isLogin) {
          loginStore.LoginModel = LoginModel.login
          GlobalConfig.history.push('/login');
          return;
        }
      }}>
        <div className="avatar">
          {userStore.userInfo ? <img src={userStore.userInfo.avatar} alt="" /> : <img className='default' src={Icon_avatar} alt="" />}
        </div>
        {!collapsed && <div className="rightContent">
          <div className="unLogin">
            {userStore.userInfo ? userStore.userInfo.username : translate("sideBar.a")}</div>
          <div className="bottom">
            {userStore.userInfo ? <span>{userStore.userInfo.profile}</span> : <a href="#/login">{translate("sideBar.b")}</a>}

          </div>
        </div>}
      </div>
    </div>

    <Menu className='topMenu' defaultSelectedKeys={[`${mainStore.menuKey}`]} mode="inline">

      {TabsConfig.sideTabs.map((t, i) => {
        const link = t.getLink ? t.getLink() : t.link;
        if (!link) {
          return <div className="more">{!collapsed && <span>{translate('common.b')}</span>} <span className="icon">{IconMore}</span> </div>
        }
        return (
          <Menu.Item key={i} icon={<img src={mainStore.menuKey == i ? t.icon : t.iconOff} alt="" />} onClick={() => {
            if (t.needLogin) {
              let isLogin = LocalStorageService.get("token");
              if (!isLogin) {
                loginStore.LoginModel = LoginModel.login
                GlobalConfig.history.push('/login');
                return;
              }
            }
            mainStore.menuKey = i;
            GlobalConfig.history.push(link || "");
          }}>
            {translate(t.name)}
          </Menu.Item>
        );
      })}
    </Menu>

    <img className={`fold ${collapsed ? 'collapsed' : ''}`} src={collapsed ? imgExpand : imgFold} alt="" onClick={() => {
      if (collapsed) {
        mainStore.tabWidth = document.body.clientWidth - px(236);
      } else {
        mainStore.tabWidth = document.body.clientWidth - 78;

      }
      setCollapsed(!collapsed);
    }} />

    {!collapsed && userStore.userInfo && <div className="logout" onClick={() => {
      mainStore.menuKey = 0;
      userStore.logout()
      GlobalConfig.history.push("/main/index/recommend")
    }}>{translate('mine.setting.i')}</div>}
    {/* {collapsed && <img className='expand' src={imgExpand} alt="" onClick={() => {
      setCollapsed(!collapsed);
      mainStore.tabWidth = document.body.clientWidth - getRem(236);
      console.log("mainStore.tabWidth222", mainStore.tabWidth)
    }} />} */}



    <Profile visible={visible} onClose={() => {
      setVisible(false);

    }} />
  </Sider>
});

export default SideTabs;
