import { observer } from "mobx-react";
import { useEffect} from "react";
import {  useParams } from "react-router";
import { GridView } from "../../components/GridView";
import { Loading } from "../../components/Loading";
import NoData from "../../components/NoData";
import GlobalConfig from "../../config/GlobalConfig";
import {  CreatorVideoService } from "../../services/CreatorVideoService";
import { userStore } from "../../stores/UserStore";
import {  useRequest } from "../../utils/hooks";
import { getAgo} from "../CreatorCenter/CreatorCenter";
import { StripePayArea } from "../Stripe/Stripe";
import { Image } from "../../components/Image";

import './UserVideoList.scss';
import eventBus from "../../utils/eventBus";
import { translate } from "../../components/Text";


const getVideosData = ({ id = '', searchkey = '' }) => new CreatorVideoService({ psize: 999, userid: id, searchkey: searchkey }).send();


export const UserVideoList = observer(() => {
  const uid = userStore.userInfo?.id;
  const { id = '' } = useParams<any>();
  const { loading, data = [], load } = useRequest(getVideosData, { autoLoad: false });
  const isCurUser = (uid ?? '').toString() === id.toString();
  useEffect(() => {
    load({ id });
    eventBus.addListener('userSearch', (key: any) => {
      load({ id, searchkey: key })
    });
  }, []);

  return (
    <div className="user-video-list">
      <div className="label-video-list user-video-list">
        {loading ? <Loading size="mini" /> : data.length === 0 ? <NoData text={translate('common.c')} /> :
          <GridView id="videoList" space={10} itemCount={data.length} itemBuilder={(idx) => {
            let it: any = data[idx];
            let price = it.lookauth === 1 || it.islookauth === 1 || isCurUser || parseInt(it.tryminute, 10) > 0 ? '0' : it.price
            return (
              <div className='video-item' key={it.id}>
                <StripePayArea
                  price={price}
                  objectid={it.id}
                  ordertype={3}
                  targetInfo={{ title: it.title, avatar: it.avatar, subTitle: it.username }}
                  onClick={() => GlobalConfig.history.push(`/main/videoPlay/videocreator/${it.id}`)}
                  onComplete={() => GlobalConfig.history.push(`/main/videoPlay/videocreator/${it.id}`)}
                >
                  <Image className="thumb" src={it.thumb} />
                </StripePayArea>
                <div className="info">
                  <div>
                    <div className="title">{it.title}</div>
                    <div className="subInfo">{it.viewcount}次播放 · {getAgo(it.createtime)}</div>
                  </div>
                  <div className="price"> {price != '0' ? it.price : 'FREE'}</div>
                </div>
                <div className="describe">{it.abstract}</div>
              </div>
            );
          }} />
        }
      </div>
    </div>
  );
});

