import { Ajax } from '../utils/http';

export class CommentListService {
    private objectid: string;
    private objecttype: number;
    private lastid?: string;
    constructor(_objectid: string, _objecttype: number, _lastid?: string) {
        this.objectid = _objectid;
        this.objecttype = _objecttype;
        this.lastid = _lastid;
    }

    public async send(): Promise<Array<CommentItem>> {
        let response: Array<CommentItem>;
        let data = {
            objectid: this.objectid,
            objecttype: this.objecttype,
        }
        if (this.lastid) {
            data['lastid'] = this.lastid;
        }
        try {
            response = await new Ajax<Array<CommentItem>>('/comment/getCommentList', data).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}

export class CommentService {
    private objectid: string;
    private objecttype: number;
    private content: string;
    constructor(_objectid: string, _objecttype: number, _content: string) {
        this.objectid = _objectid;
        this.objecttype = _objecttype;
        this.content = _content;
    }

    public async send() {
        let response: string;
        let data = {
            objectid: this.objectid,
            objecttype: this.objecttype,
            content: this.content,
        }

        try {
            response = await new Ajax<string>('/comment/saveComment', data).post();
        } catch (e) {
            throw e;
        }

        return response;
    }
}

