export class CommentReplyService {
    private objectid: number;
    private objecttype: string;
    private content: string;
    private beid: string;
    private pid: string;
    constructor(_objectid: number, _objecttype: string, _content: string, _beid: string, _pid: string) {
        this.objectid = _objectid;
        this.objecttype = _objecttype;
        this.content = _content;
        this.beid = _beid;
        this.pid = _pid;
    }

    public async send() {
        let data = {
            objectid: this.objectid,
            objecttype: this.objecttype,
            becontent: this.content,
            beid: this.beid,
            pid: this.pid
        }
        let response: string;
        try {
            response = await new Ajax<string>('/comment/replyComment', data).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}



