import { Button } from 'antd';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { useHistory } from 'react-router';
import { translate } from '../../components/Text';
import { Loading_icon } from '../../images/Loading';
import { CommentItem } from '../../services/CommentService';
import { commentStore } from '../../stores/CommentStore';
import { userStore } from '../../stores/UserStore';
import eventBus from '../../utils/eventBus';
import { useScrollToBottomEvent } from '../../utils/hooks';
import './Comment.scss';
import { CommentItemPage } from './CommentItem';

export interface CommentPageProps {
    isSelf: boolean
    objectid: string,

    objecttype: number,
}

export const CommentPage = observer((props: CommentPageProps) => {
    const his = useHistory();
    useEffect(() => {
        commentStore.objectid = props.objectid;
        commentStore.objecttype = props.objecttype;
        commentStore.getData();
    }, []);

    useScrollToBottomEvent(() => commentStore.getData(true));
    if (commentStore.loading) {
        return <div></div>
    }
    return (
        <div className="commentPage">
            <div className="ttp-comment-block">
                <div id="comment-area" className="ttp-comment-wrapper">
                    <div className="title">{translate('home.video.b')} <span>{commentStore.commentList.length}</span>
                    </div>
                    <div className="main-input">
                        <div className="ttp-avatar auth-none">
                            <img src={userStore.userInfo?.avatar} alt="" />
                        </div>
                        <div className="ttp-comment-input">
                            <textarea className="comment-textarea" id="commentInput" onChange={(v) => {
                                commentStore.commentContent = v.target.value;
                            }} placeholder={translate('home.video.c')}>
                            </textarea>
                            <div className="action">
                                <Button className="submit-btn disable" loading={commentStore.commentting} onClick={() => {
                                    if (!userStore.userInfo) {
                                        his.push('/login');
                                        return;
                                    }
                                    commentStore.comment();
                                }}>{translate('home.video.d')}</Button>

                                {/* <button type="button" onClick={() => {
                                    if (!userStore.userInfo) {
                                        his.push('/login');
                                        return;
                                    }
                                    commentStore.comment();
                                }} className="submit-btn disable">{translate('home.video.d')}</button> */}
                            </div>
                        </div>
                    </div>
                    <ul className="comment-list">
                        {commentStore.commentList.map((comment: CommentItem, i) =>
                            <CommentItemPage key={i} isSelf={props.isSelf} item={comment} success={() => {
                            }} onDelete={() => {
                                let list = JSON.parse(JSON.stringify(commentStore.commentList));
                                list.splice(i, 1);
                                commentStore.commentList = list;
                            }} />
                        )
                        }
                    </ul>
                </div>

            </div>
            {commentStore.loadingMore && <div className="loadmore-comments">
                <img src={Loading_icon} alt="" />
            </div>}
        </div>

    );
}
)