import { Form, Input, Button, Select } from 'antd';
import { UserOutlined, LockOutlined, MailOutlined, PhoneOutlined } from '@ant-design/icons';
import { LoginModel, loginStore, RegisterType } from '../../stores/LoginStore';
import { observer } from 'mobx-react';
import './Login.scss';
import { areaCodes } from './AreaCodes';
import { translate } from '../../components/Text';
import { Icon_password } from '../../images/index/password';
import { Icon_captcha } from '../../images/index/captcha';
import { useEffect, useState } from 'react';
import GlobalConfig from '../../config/GlobalConfig';
import { Spin } from 'antd';
import { useRequest } from '../../utils/hooks';
import { CaptchaResponse, SendCaptchaService } from '../../services/LoginService';

const { Option } = Select;

export const RegisterPage = observer((props: any) => {
    var areacode = "61";
    const { loading, data, load } = useRequest(() =>
        new SendCaptchaService().send()
    );
    const form = Form.useForm()[0];
    useEffect(() => {
        load({});
    }, [])
    const prefixSelector = (
        <Form.Item name="prefix" noStyle>
            <Select style={{ width: 70 }} defaultValue="61" onChange={(e) => {
                areacode = e;
            }}>
                {areaCodes.map((e, i) => <Option key={i} value={e.areacode}>+{e.areacode}</Option>)}
            </Select>
        </Form.Item>
    );
    return (
        <div className="info">
            <Form
                name="normal_login"
                form={form}
                className="login-form"
                initialValues={{ remember: true }}
                onFinish={(v) => {
                delete v.captcha;
                    if (loginStore.registerType == RegisterType.phone) {
                        v.areacode = areacode;
                    }
                    loginStore.register(v, () => {
                        if(loginStore.fromIndex ||　document.referrer !== ''){
                            loginStore.fromIndex=false;
                            GlobalConfig.history.push('/main/index/recommend');
                        }else{
                            GlobalConfig.history.goBack();
                        }
                    })
                }}
            >
                <Form.Item
                    name="username"
                    rules={[{ required: true, message: translate('mine.register.b') }]}
                >
                    <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder={translate('mine.register.b')} />
                </Form.Item>
                {loginStore.registerType == RegisterType.email ? <div>
                    <Form.Item
                        name="email"
                        rules={[{ required: true, message: translate('mine.register.d') }]}
                    >
                        <Input prefix={<MailOutlined className="site-form-item-icon" />} placeholder={translate('mine.register.d')} />
                    </Form.Item>
                    <div className="captcha">
                        <Form.Item
                            className="codeInput"
                            name="code"
                            rules={[{ required: true, message: translate('mine.find.c') }]}
                        >
                            <Input
                                // prefix={<PhoneOutlined className="site-form-item-icon" />}
                                prefix={<img className='icon' src={Icon_captcha} alt="" />}

                                placeholder={translate('mine.find.c')} />
                        </Form.Item>
                        <Button htmlType="submit" className="forget-form-button" onClick={() => { loginStore.sendEmial(form.getFieldsValue(["email"]).email, 2) }}>
                            {translate(loginStore.sendBtnText)}
                        </Button>
                    </div>
                    <Form.Item
                        name="password"
                        rules={[{ required: true, message: translate('mine.register.f') }]}
                    >
                        <Input
                            prefix={<LockOutlined className="site-form-item-icon" />}
                            type="password"
                            placeholder={translate('mine.register.f')}
                        />
                    </Form.Item>
                </div>
                    :
                    <div>
                        <div className="phone-type">
                            <Form.Item
                                name="phone"
                                rules={[{ required: true, message: translate('mine.login.i') }]}
                            >
                                <Input addonBefore={prefixSelector} placeholder={translate('mine.login.i')} />
                            </Form.Item>
                        </div>
                        <div className="captcha phone-type">
                            <Form.Item
                                className="codeInput"
                                name="captcha"
                                rules={[{ required: true, message: translate('mine.login.j') }]}
                            >

                                <Input prefix={<img className='icon' src={Icon_captcha} alt="" />} placeholder={translate('mine.login.j')} />
                            </Form.Item>
                            <div className="captchaImg forget-form-button" onClick={() => {
                                load({});
                            }} >
                                {loading ? <Spin spinning={true} size='small'>
                                </Spin> : <img src={data?.content} className="forget-form-button" />}
                            </div>

                        </div>
                        <div className="captcha phone-type">
                            <Form.Item
                                className="codeInput"
                                name="code"
                                rules={[{ required: true, message: translate('mine.login.j') }]}
                            >
                                <Input prefix={<img className='icon' src={Icon_captcha} alt="" />} placeholder={translate('mine.login.j')} />
                            </Form.Item>
                            <Button htmlType="submit" className="forget-form-button" onClick={(e) => {
                                e.preventDefault();
                                e.stopPropagation();
                                loginStore.sendPhone(form.getFieldsValue(["phone"]).phone, 1, areacode, data?.id || '', form.getFieldsValue(["captcha"]).captcha)
                            }}>
                                {translate(loginStore.sendBtnText)}
                            </Button>
                        </div>
                        <Form.Item
                        name="password"
                        rules={[{ required: true, message: translate('mine.register.f') }]}
                    >
                        <Input
                            prefix={<LockOutlined className="site-form-item-icon" />}
                            type="password"
                            placeholder={translate('mine.register.f')}
                        />
                    </Form.Item>
                    </div>}

                <div>
                    <Button htmlType="submit" className="login-form-button">
                        {translate('mine.register.g')}
                    </Button>
                    <div className="form-wrapper">
                        {loginStore.LoginModel === LoginModel.registerPop ? <div className="toLogin">
                            <span>新注册用户享受免费7天订阅</span>
                        </div> : <div className="toLogin"><span>{translate('mine.register.h')}</span><span className="loginLabel" onClick={() => {
                            if (loginStore.registerLoading) return;
                            loginStore.setLoginModel(LoginModel.login)
                        }}> {translate('mine.login.h')}</span></div>}
                    </div>
                </div>
            </Form>
        </div>
    );
})













