import { makeAutoObservable, } from "mobx";
import { RefreshUserService, UserInfo } from "../services/LoginService";
import { LocalStorageService } from '../utils/storage';

class UserStore {
  public userInfo?: UserInfo;
  public loginModalVisible: boolean = false;
  public downloadModalVisible: boolean = false;
  public showRegisterPop: boolean = false;
  public hideRegisterPopBtn: boolean = false;
  private timer: any;

  constructor() {
    makeAutoObservable(this);
  }

  get isVip() {
    return this.userInfo?.rules && this.userInfo?.rules.length>0;
  }

  showRegisterPopLoop() {
    if (this.userInfo) {
      return;
    }
    this.hideRegisterPopBtn = false;
    clearInterval(this.timer);
    this.showRegisterPop = false;
    this.timer = setInterval(() => {
      clearInterval(this.timer);
      if (!this.userInfo) {
        this.showRegisterPop = true;
      }
    }, 15000);
  }

  public async refreshUser() {
    let response: UserInfo;
    try {
      response = await new RefreshUserService().send();
    } catch (e: any) {
      return;
    }
    userStore.updateUser(response);
  }

  stopRegisterPopLoop() {
    this.hideRegisterPopBtn = true;
    clearInterval(this.timer);
  }

  public init() {
    let _userInfo: UserInfo = LocalStorageService.get('_userInfo');
    this.userInfo = _userInfo;
  }


  public updateUser(_userInfo: UserInfo) {
    this.userInfo = _userInfo;
    if (_userInfo) {
      LocalStorageService.set('_userInfo', _userInfo)
    }
  }

  public logout() {
    this.userInfo = undefined;
    LocalStorageService.remove("_userInfo");
    LocalStorageService.remove("token");
  }
}

const userStore = new UserStore();

export { userStore };