import { FindType, LoginModel, loginStore, LoginType, RegisterType } from '../../stores/LoginStore';
import './Login.scss';
import { Icon_logo } from '../../images/index/logo';
import { LoginPage } from './Login';
import { RegisterPage } from './Register';
import { ForgetPasswordPage } from './ForgetPassword';
import { observer } from 'mobx-react';
import { Headers } from '../Header/Header';
import { translate } from '../../components/Text';
import { Icon_logo_gold } from '../../images/index/logoGold';

interface loginProps {
    close?: Function;
}

export const LoginIndexPage = observer((props: loginProps) => {
    
    var title;
    if (loginStore.LoginModel === LoginModel.login) {
        title = <div className="login-tabs">
            <div className={loginStore.loginType == LoginType.email? "item active":"item"} onClick={()=>loginStore.loginType = LoginType.email}>{translate('login.a')}</div>
            <div className={loginStore.loginType == LoginType.phone? "item active":"item"} onClick={()=>loginStore.loginType = LoginType.phone}>{translate('login.b')}</div>
        </div>
    } else if (loginStore.LoginModel === LoginModel.register  ) {
        title = <div className="register-tabs">
            <div className={loginStore.registerType == RegisterType.email? "item active":"item"} onClick={()=>loginStore.registerType = RegisterType.email}>{translate('login.c')}</div>
            <div className={loginStore.registerType == RegisterType.phone? "item active":"item"} onClick={()=>loginStore.registerType = RegisterType.phone}>{translate('login.d')}</div>
        </div>
    } else {
        title = <div className="register-tabs">
        <div className={loginStore.findType == FindType.email? "item active":"item"} onClick={()=>loginStore.findType = FindType.email}>{translate('login.e')}</div>
        <div className={loginStore.findType == FindType.phone? "item active":"item"} onClick={()=>loginStore.findType = FindType.phone}>{translate('login.f')}</div>
    </div>
    }

    
    return (
        <div className="loginPage">
      <Headers />
            <div className="loginWrapper">
                <div className="logo">
                    <img src={Icon_logo_gold} alt="" />
                    <div className="name">OLI VIEW</div>
                </div>
                <div className="loginContent">
                    <div className="title">{title}</div>
                    {loginStore.LoginModel === LoginModel.login ?
                        <LoginPage /> : loginStore.LoginModel === LoginModel.register ?
                            <RegisterPage /> : <ForgetPasswordPage />}
                </div>
            </div>
        </div>)
})